import React, {Component} from 'react';
import {Carousel, WingBlank, Flex, WhiteSpace, Icon} from 'antd-mobile';
import constant from '../../constant/constant';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import ProductLink from '../../component/ProductLink'
import './adminMenage.css';
const FlexItem = Flex.Item;

class BootstrapPage extends Component {
    render() {
        return (
            <div>
                <WhiteSpace size="lg"/>
                <div style={{width: "100%", textAlign: "center", color: "#bbb"}}></div>
                <hr/>
                <div>
                    <WhiteSpace size="lg"/>
                    <WingBlank size="lg">
                        <Flex>
                            <FlexItem style={{margin:'20px 10px'}}>
                                <p>Content Manage SystemCMSCMSContent Management System"" CmsTopTurboCMSTRS</p>
  
                            </FlexItem>
                        </Flex>
                        <Flex>
                            <FlexItem style={{margin:'20px 10px'}}>
                                <img width="100%" src={constant.QINIU+'con1-bg1.png'}/>
                            </FlexItem>
                            <FlexItem style={{margin:'20px 10px'}}>
                                <h4></h4>
                                <p>360<br/><br/></p>
                            </FlexItem>
                        </Flex>
                        <Flex>
                            <FlexItem style={{margin:'20px 10px'}}>
                                <h4></h4>
                                <p> <br/> <br/></p>
                            </FlexItem>
                            <FlexItem style={{margin:'20px 10px'}}>
                                <img width="100%" src={constant.QINIU+'con1-bg2.png'}/>
                            </FlexItem>
                        </Flex>
                        <Flex>
                            <FlexItem style={{margin:'20px 10px'}}>
                                <img width="100%" src={constant.QINIU+'con1-bg3.png'}/>
                            </FlexItem>
                            <FlexItem style={{margin:'20px 10px'}}>
                                <h4></h4>
                                <p><br/><br/></p>
                            </FlexItem>
                        </Flex>
                        <Flex style={{textAlign:'right'}}>
                            <FlexItem style={{margin:'20px 10px'}}>
                                {ProductLink('admin1')}
                            </FlexItem>
                        </Flex>
                    </WingBlank>
                </div>
            </div>
        );
    }

}


export default BootstrapPage


